<template>
  <div>
    <main class="content">
      <section class="logo">
        <div class="container">
          <div class="row align-items-center">
            <div class="col-12 col-lg-5">
              <h2>Разработка Логотипа</h2>
              <p>Вы хорошо знаете свой бизнес, а мы знаем как сделать хороший дизайн! И будем рады сделать лого для вашей: Компании, Приложения или Сайта.</p>
              <div class="buttons">
                <router-link :to="{ path: briefLink }" class="button" @click.native="scrollToTop()">Заполнить бриф</router-link>
                <button class="button button-transparent" @click="scrollToPrices ()">Цены</button>
              </div>
            </div>
            <div class="col-12 col-lg-7 d-flex justify-content-center justify-content-lg-end">
              <div class="img-container">
                <picture>
                  <source srcset="img/static/Logo/1.webp">
                  <img srcset="img/static/Logo/1.png" alt="">
                </picture>
              </div>
            </div>
          </div>
        </div>
      </section>
      <ProcessDev />
      <SectionPortfolio
          v-bind:portfolio = 'portfolio'
      />
      <Prices
          v-bind:priceProducts = 'priceProducts'
      />
      <OurFeatures class="features" />
    </main>
    <MailForm />
  </div>
</template>

<script>
import MailForm from '@/components/MailForm'
import ProcessDev from '../components/ProcessDev'
import SectionPortfolio from '../components/SectionPortfolio'
import OurFeatures from '../components/OurFeatures'
import Prices from '../components/Prices'
export default {
  components: { MailForm, ProcessDev, SectionPortfolio, Prices, OurFeatures },
  props: ['portfolio'],
  data () {
    return {
      priceProducts: [
        { name: 'Минимальный', price: '5 000', feature1: '2 концепции, 3 правки', feature2: '3 дня', feature3: 'Исходные файлы (*ai)', feature4: 'Логотип в цветовом пространстве CMYK' },
        { name: 'Стандартный', price: '13 000', feature1: '3 концепции, 5 правок', feature2: '2 дня', feature3: 'Услуги пакета "Минимальный"', feature4: 'Внесение правок после утверждения макета' },
        { name: 'Максимальный', price: '20 000', feature1: '5 концепции, 7 правок', feature2: 'Услуги пакета "Стандартный"', feature3: 'Подбор фирменных шрифтов', feature4: 'Арт-дирекшн и контроль со стороны CEO' }
      ]
    }
  },
  computed: {
    briefLink () {
      return this.$store.getters.briefLink
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo(0,0);
    },
    scrollToPrices () {
      let headerHeight = document.querySelector('.header').offsetHeight,
          pricesPosition = this.$el.querySelector('.prices').getBoundingClientRect().top - headerHeight,
          scrollOptions = {
            left: 0,
            top: pricesPosition,
            behavior: 'smooth'
          }
      window.scrollTo( scrollOptions)
    }
  },
  mounted() {
    this.$store.dispatch('formTopPosition')
  }
}
</script>

<style scoped lang="scss">
.content {
  padding-bottom: 14rem;
}
.logo {
  margin-top: 8rem;
  h2 {
    margin-bottom: 3rem;
  }
  p {
    padding: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 185%;
    letter-spacing: 0.02em;
    color: #1C1D28;
    width: 32rem;
    margin-bottom: 1rem;
  }
  cite {
    font-weight: bold;
    font-size: 15px;
    line-height: 185%;
    letter-spacing: 0.02em;
    color: #1C1D28;
  }
  .buttons {
    margin-top: 3rem;
    margin-bottom: 18rem;
    @media (max-width: 998px) {
      margin-bottom: 8rem;
    }
    @media (max-width: 576px) {
      margin-bottom: 4rem;
    }
  }
  .button-transparent {
    margin-left: 2rem;
    padding-right: 7rem;
    padding-left: 7rem;
  }
  .img-container {
    position: relative;
    img {
      position: relative;
      width: 50rem;
      height: 45rem;
      object-fit: contain;
      object-position: center;
      @media (max-width: 768px) {
        width: 30rem;
      }
      @media (max-width: 576px) {
        width: 22rem;
      }
    }
  }
}
.features {
  margin-top: 14rem;
}
</style>
